.banner-about {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.small {
  background-color: #1aa260;
  width: 70%;
  margin-top: 5rem;
  height: 60%;
  display: flex;
  justify-content: center;
  border-radius: 20px;
  align-items: center;
}
.content {
  width: 90%;
  height: 80%;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.content img {
  width: 25vw;
  min-width: 300px;
  border-radius: 20px;
  object-fit: contain;
}
.description > .large {
  font-size: 40px;
}
.content .description {
  width: 50%;
  display: flex;
  height: 100%;
  align-items: left;
  justify-content: space-evenly;
  flex-direction: column;
  font-size: 25px;
}

button.hire-me {
  width: 130px;
  color: white;
  background-color: tomato;
  border: none;
  cursor: pointer;
  font-size: 20px;
  outline: none;
  transition: all 0.4s;
  opacity: 0.9;
  padding: 12px 25px;
}

button.hire-me:hover {
  color: tomato;
  background-color: #ffffff;
}
